.contact-table {
  border-radius: 1%;
  min-width: 500;
  width: 75%;
  height: 70vh;
  margin: 20px auto;
  padding: 15px 10px;
  background: #fff;
}

.button {
  font: inherit;
  padding: 0.5rem 1.5rem;
  border: 1px solid #ff8800;
  border-radius: 4px;
  background: #ff8800;
  color: white;
  cursor: pointer;
  margin-right: 1rem;
  text-decoration: none;
  display: inline-block;
}

.button:focus {
  outline: none;
}

.button:hover,
.button:active {
  background: #e46124;
  border-color: #e46124;
}

.button--inverse {
  background: transparent;
  color: #ff0055;
}

.button--inverse:hover,
.button--inverse:active {
  color: white;
  background: #ff0055;
}

.button--danger {
  background: #830000;
  border-color: #830000;
}

.button--danger:hover,
.button--danger:active {
  background: #f34343;
  border-color: #f34343;
}

.button:disabled,
.button:hover:disabled,
.button:active:disabled {
  background: #ccc;
  color: #979797;
  border-color: #ccc;
  cursor: not-allowed;
}

.button--small {
  font-size: 0.8rem;
}

.button--big {
  font-size: 1.5rem;
}

.main-window {
  height: 80vh;
}

